<template>
  <div class="main">
    <!-- {{id}} -->
    <div @click.stop="back" class="back">
      <img src="../assets/img/back.png" alt="" />
    </div>
    <!-- 背景图 -->
    <div class="back-bg">
    <div
      class="img-bg"
      :style="`background-image:url(${playlist.coverImgUrl})`"
    ></div>
    </div>
    <div class="img">
      <img :src="playlist.coverImgUrl" />
      <p>{{ playlist.description | formatWords }}</p>
      <p v-if="!playlist.description">《{{playlist.name}}》惬意的诗歌...</p>
    </div>
    <div class="recomlist">
        <!-- 评论 + 全部播放 -->
      <div v-show="!showrecom" class="midle">
        <div class="imgrec">
          <img @click="showrecom = true" src="../assets/img/recom.png" alt="" />
          <i>{{ hotComments.length + comments.length }}</i>
        </div>

        <p @click.stop="$emit('addList', song_list)">
          <img src="../assets/img/play2.png" alt="" />
          全部播放
        </p>
      </div>
      <!-- 显示评论 -->
      <div class="recoment" v-show="showrecom">
        <svg
          @click.stop="showrecom = false"
          t="1620788071533"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="1865"
        >
          <path
            d="M512 736c-9.6 0-16-3.2-22.4-9.6l-384-384c-12.8-12.8-12.8-32 0-44.8s32-12.8 44.8 0L512 659.2 873.6 297.6c12.8-12.8 32-12.8 44.8 0s12.8 32 0 44.8l-384 384c-6.4 6.4-12.8 9.6-22.4 9.6z"
            fill="#AECE89"
            p-id="1866"
          ></path>
        </svg>
        <recom-detail
          :hotComments="hotComments"
          :comments="comments"
        ></recom-detail>
      </div>
    </div>
    <!-- 歌单 -->
    <div class="slist" v-show="showlist" v-if="!showrecom">
      <song-list
        v-for="song in song_list"
        :key="song.id"
        :song="song"
        :playMusic="playMusic"
        @playing="$emit('playing', $event)"
        @addList="$emit('addList', $event)"
      ></song-list>
     
    </div>
  </div>
</template>

<script>
import SongList from "@/components/SongList";
import RecomDetail from "@/components/RecomDetail";
export default {
  components: {
    SongList,
    RecomDetail,
  },
  props: ["id","playMusic"],
  data() {
    return {
      song_list: [],
      playlist: "",
      hotComments: "",
      comments: "",
      showlist: true,
      showrecom: false,
    };
  },
  methods: {
    back() {
      this.$router.go(-1);
    },

  },
  filters:{
formatWords(v){
    if(!v)return "";
    if(v.length>20){
        return v.slice(0,22)+" ..."
    }
    return v;
}
  },

  created() {
    this.$axios.get("playlist/detail?id=" + this.id).then((data) => {
      this.song_list = data.data.playlist.tracks;
      this.playlist = data.data.playlist;
      
    });
    this.$axios.get("/artist/top/song?id=6452").then((dat) => {
      this.song_list = dat.data.songs;
      this.playlist = dat.data.songs.map(song=>{
        song.coverImgUrl  = song.al.picUrl;
        return song;
      });
      


    });
      
    //歌单评论
    this.$axios.get("/comment/playlist?id=" + this.id).then((d) => {
      this.comments = d.data.comments;
      this.hotComments = d.data.hotComments;
    });
  },
};
</script>

<style lang="less" scoped>
div.main {
  position: relative;
  .back {
    width: 30px;
    height: 20px;
    position: absolute;
    top: 8px;
    left: 5px;
    z-index: 10;
    img {
      width: 22px;
      height: 22px;
    }
  }
  .recomlist {
    // 评论图标
    .recoment {
    position: relative;
      svg {
        position: absolute;
        top: 10px;
        left: 20px;
        width: 25px;
        height: 25;
        z-index: 55;
      }
    }
    .midle {
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding: 6px 0;
      background-color: #fff;
      box-shadow: 0px 2px 3px -1px #999;
      width: 70vw;
      margin: 0 auto;
      border-radius: 10px;
      z-index: 9;
      position: absolute;
      top: 40vw;
      left: 15vw;
      .imgrec {
        padding: 5px;
        position: relative;
        i {
          position: absolute;
          top: 6px;
          right: -18px;
        }
        img {
          width: 25px;
          height: 25px;
        }
      }
      p {
        position: relative;
        img {
          position: absolute;
          top: -2px;
          left: -30px;
          width: 25px;
        }
      }
    }
  }
  .back-bg{

     height: 50vw;
      overflow: hidden;
  .img-bg{
     width: 100vw;
     height: 100%;
     filter: blur(20px);
     background-repeat:no-repeat;
     background-size: cover;
     transform: scale(1.5,1);
     &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      background-color: rgba(0,0,0,.25);
    }
    }
  }
  .img {
    position: absolute;
    top: 8vw;
    left:3vw;
    padding: 5px;
    font-size: 15px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    img {    
      width:30vw;
      border-radius: 10px;
      object-fit: cover;
    }
    p {
      // width:50vw;
      height: 40px;
      line-height: 1.5;
      text-indent: 2em;
      color: white;
      margin-left: 20px;
  }

}
  .slist {
    padding-top: 5vw;
    box-sizing: border-box;
    // position: relative;
    // z-index: 9999;
  }
}
</style>