<template>
  <div class="list">
    <div class="song">
      <div class="left">
        <i v-html="$options.filters.highLight(song.name, val)"></i>

        <!-- 第二行 -->
        <p>
          <!-- <i>HQ</i> -->
          <svg t="1621176996732" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5630" width="16" height="16"><path d="M170.666667 213.333333v597.333334h682.666666V213.333333H170.666667zM128 128h768a42.666667 42.666667 0 0 1 42.666667 42.666667v682.666666a42.666667 42.666667 0 0 1-42.666667 42.666667H128a42.666667 42.666667 0 0 1-42.666667-42.666667V170.666667a42.666667 42.666667 0 0 1 42.666667-42.666667z m192 352h85.333333V384H469.333333v256H405.333333v-96h-85.333333V640H256V384h64v96zM693.333333 640v64h-64V640H597.333333a42.666667 42.666667 0 0 1-42.666666-42.666667v-170.666666a42.666667 42.666667 0 0 1 42.666666-42.666667h128a42.666667 42.666667 0 0 1 42.666667 42.666667v170.666666a42.666667 42.666667 0 0 1-42.666667 42.666667h-32z m-74.666666-192v128h85.333333v-128h-85.333333z" p-id="5631" fill="#AECE89"></path></svg>
          <span class="first-span"
            v-for="artis in song.ar"
            :key="artis.id"
            v-html="$options.filters.highLight(artis.name, val)"
          ></span
          > - <span
            v-if="song.al.name"
            v-html="$options.filters.highLight(song.al.name, val)"
          ></span>
        </p>
      </div>
      <div class="right">
        <div class="playbts">
          <img
        v-if="!(playMusic && playMusic.id == song.id)"
            @click.stop="$emit('playing', song)"
            src="../assets/img/play2.png"
            alt=""
          />
          <loading v-if="playMusic && playMusic.id == song.id"></loading>
        </div>

        <img
          @click.stop="$emit('addList', song)"
          src="../assets/img/list.png"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Loading from "@/components/Loading";
export default {
  props: ["song", "val", "playMusic"],
  components: {
    Loading,
  },
  filters: {
    highLight(value, k) {
      //把 关键词 替换成 <span style="color:rgb(151, 204, 91)">关键词</span>
 if(k){
     let words = k;
     let reg = new RegExp(words,'i');
      return value.replace(reg,'<span style="color:rgb(151, 204, 91);">' + value + "</span>");

 }
 return value;
    },

  },
};
</script>

<style lang="less" scoped>
.song {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  box-sizing: border-box;
  .left {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-break: normal;
    i {
      font-size: 16px;
      color: #333;
      font-weight: 400;
    }
    p {
      font-size: 12px;
      color: #666;
      position: relative;
      svg{
        position: absolute;
        top: 0;
        left: 0;
      }
      span.first-span{
        margin-left: 18px;
      }
    }
  }
  .right {
      width: 100px;
      display: flex;
      justify-content: space-evenly;
    img {
      width: 20px;
      margin-left: 10px;
    }
  }
}
</style>